.table-loading {
  position: relative;
}
.table-loading .table-loader-wrap {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 9;
}
.table-loading .table-loader-wrap .table-loader-center {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 100%;
}
.table-loading .wp-list-table,
  .table-loading .tablenav {
    opacity: 0.4;
}
.table-loader {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #ffffff;
  background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  position: relative;
  animation: tableLoading 1s infinite linear;
  transform: translateZ(0);
}
.table-loader:before {
    width: 50%;
    height: 50%;
    background: #ffffff;
    border-radius: 100% 0 0 0;
    position: absolute;
    top: 0;
    left: 0;
    content: '';
}
.table-loader:after {
    background: #f4f4f4;
    width: 75%;
    height: 75%;
    border-radius: 50%;
    content: '';
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
@-webkit-keyframes tableLoading {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}
@keyframes tableLoading {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}








































































































































































